<template>
  <v-container fluid class="down-top-padding">
    <v-row>
      <input type="file" name="" class="d-none"  ref="files" @change="fileInput" id="">
      <v-col cols="12" lg="4">
        <v-card>
          <v-card-text class="text-center pa-7">
            <v-list-item-avatar size="170" color="grey darken-3">
          <v-img
            v-if="colorShow"
            class="elevation-6"
            :src="teacher.image==''?'https://s3.amazonaws.com/37assets/svn/765-default-avatar.png':teacher.image"
             ></v-img>
        </v-list-item-avatar>
              <div class="text-center">
            <v-btn class="text-capitalize mt-5 element-0" style="color:white"  @click="$refs['files'].click()" color="danger"> + Resim {{teacher.images!='null'?'Düzenle':'Ekle'}}</v-btn>
  </div>
  <div class="text-center text danger" style="margin-top:10px;color:white">{{errImage}}</div>
            <h4 class="mt-2 title blue-grey--text text--darken-2 font-weight-regular">{{teacher.name}} {{teacher.lastname}}</h4>
            <h6 class="subtitle-2 font-weight-light">{{$t('header.teacher')}}</h6>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="12" lg="8">
        <v-card>
          <v-card-text>
            <h3 class="title blue-grey--text text--darken-2 font-weight-regular">{{$t('dashboard.insertTeacher')}}</h3>
            <h6 class="subtitle-2 font-weight-light">{{$t('teacher.info')}}</h6>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-text>
            <v-text-field
              v-model="teacher.name"
              :error-messages="errName"
              :label="$t('teacher.name')"
              filled
              background-color="transparent"
            ></v-text-field>
               <v-text-field
              v-model="teacher.lastname"
              :error-messages="errLastname"
               :label="$t('teacher.lastname')"
              filled
              background-color="transparent"
            ></v-text-field>
            <v-text-field
              type="teacher.username"
              v-model="teacher.username"
             :label="$t('generate.username')"
              filled
              :success-messages="successmessages"
              :error-messages="errormessages"
              @blur="controlUsername"
              background-color="transparent"
            ></v-text-field>
            <v-text-field
              v-model="teacher.password"
              filled
              :error-messages="errPassword"
              background-color="transparent"
              :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
              :type="show1 ? 'text' : 'password'"
              name="input-10-1"
                :label="$t('generate.password')"
              :hint="$t('err.password')"
              counter
              @click:append="show1 = !show1"
            ></v-text-field>
           <v-text-field
            @blur="phoneControl()"
              type="teacher.phone"
              :error-messages="errPhone"
              v-model="teacher.phone"
             :label="$t('generate.phone')"
              filled
              background-color="transparent"
            ></v-text-field>

            <v-btn class="text-capitalize mt-5 element-0" :loading="loading" :disabled="loading" @click="sendTeacher()" color="success">{{$t('generate.submit')}}</v-btn>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import {phoneNumberControl} from '../../plugins/rules'

export default {
  name: "Profile",

  data: () => ({
    errName:[],
    errLastname:[],
    errPhone:[],
    errPassword:[],
    errImage:"",
    teacher:{
      name:"",
      lastname:"",
      username:"",
      password:"",
      phone:"",
      file:[],
      image:"",
    },
    emailtext: "",
    password: "",
    disableinput: "",
    checkbox1: "",
    checkbox2: "",
    checkbox3: "",
    colorShow:true,
    show1: false,
    loading:false,
    errormessages:[],
    successmessages:[]
  }),
  methods:{
      phoneControl(){
    let result = phoneNumberControl(this.teacher.phone)
    if(result){
      this.errPhone.push(result)
    }
    else{
      this.errPhone=[]
    }
  },
      controlUsername(){
    this.loading=true
     if(this.teacher.username.length>3){
        this.$api.get('user?filter=username="'+this.teacher.username+'"').then(res=>{
        if(res.count==0){
            this.successmessages=[]
                this.errormessages=[]
          this.successmessages.push('Bu kullanıcı adı kullanılabilir.')
           this.loading=false
        }
        else{
            this.successmessages=[]
          this.errormessages=[]
          this.errormessages.push(`${this.teacher.username} adlı kullanıcı ismi kullanılıyor.`)
           this.loading=false

        }
      })
     }
     else
        this.errormessages.push('Kullanıcı Adı 3 karakterden fazla olmalıdır.')
    this.loading=false

    },
   async sendTeacher(){
     this.errName=[]
     this.errLastname=[]
     this.errPassword=[]
     this.errImage=[]
      this.loading = true
    if(this.teacher.name==''||this.teacher.lastname==''||this.teacher.username==''||this.teacher.phone.length<9||this.errPhone.length!=0||this.teacher.password<5||this.teacher.file.length==0 || this.errormessages.length!=0){
      if(this.teacher.name==''){
        this.errName.push('Öğretmen Adı boş geçilemez.')
      }
      if(this.teacher.lastname==''){
        this.errLastname.push('Öğretmen Soyadı boş geçilemez.')
      }
      if(this.teacher.phone.length<9){
        this.errPhone.push('Eksik veya yanlış telefon numarası girdiniz.')
      }
      if(this.teacher.password.length<5){
        this.errPassword.push('Şifreniz en az 6 karakter olmalıdır.')
      }
       if(this.teacher.file.length==0){
        this.errImage = 'Öğretmenin resmini eklemelisiniz.'
      }
      this.loading = false
    }
    else{
      let fd = new FormData();
      fd.append('name',this.teacher.name)
      fd.append('lastName',this.teacher.lastname)
      fd.append('phone',this.teacher.phone)
      fd.append('username',this.teacher.username)
      fd.append('password',this.teacher.password)
      fd.append('isTeacher',1)
      fd.append('image',this.teacher.file)
      let result = await this.$api.postfd('user','image',fd)
      if(result.status=='success'){
       this.loading = false
        this.$router.push('/pages/class-list')
      }
      else{
     this.loading = false

      }
    }

    },
       createImage(file) {

      var reader = new FileReader();
      var vm = this;
      reader.onload = e => {
        vm.teacher.image = e.target.result;
             this.colorShow=true
      };
      reader.readAsDataURL(file);


    },
    fileInput(event){
      this.colorShow=false
       this.teacher.file = event.target.files[0]
       this.createImage(this.teacher.file)
    },
  }
};
</script>